//上传进度提示
.tip{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    font-size: 14px;
    width: 100%;
    height: 100%;
    .mask{
        background: rgba(0, 0, 0, .65);
        @include abs_full_page();
    }
    &.uploading{
        display: none;

        .content{
            padding: 1.5rem 1.5rem;
            width: 50vw;
            background: rgba(0,0,0,.8);
            border-radius: 5px;
            position: relative;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            text-align: center;
            .progress-bg{
                position: relative;
                height: 8px;
                width: 100%;
                top: 0;
                background-color: #fff;
                border-radius: 5px;
            }
            .progress-bar{
                position: absolute;
                left: 0;
                top: 0;
                height: 8px;
                width: 80%;
                display: inline-block;
                background-color: $main-font-color-r;
                border-radius: 5px;
            }
            .snowman{
                position: absolute;
                width: 30%;
                left: 14%;
                top: -4%;
            }
            p{
                margin: 0;
                margin-bottom: 1rem;
                margin-left: 3rem;
                font-size: .6rem;
                span{
                    font-size: 1.2rem;
                    font-weight: 600;
                }
            }
        }
    }
    &.uploaded{
        display: none;
        .content{
            position: relative;
            margin: 0 2.5rem 1.5rem;
            width: 50vw;
            text-align: center;
            .redpacket{
                position: relative;
                margin-top: -30%;
                width: 50%;
            }
            p{
                margin: 0;
                margin-top: 1rem;
                line-height: 2;
            }
        }
    }
}

