.myprize.popup {
  display: none;

  .box {
    .wrap {
      .btn_close {
        top: 5%;
      }

      .tabtitle {
        width: 75%;
        position: absolute;
        top: 16%;
        left: 12.5%;
        //              border-bottom: solid 1px #77bc86;
        ul {
          margin: 0;
          padding: 0;
          overflow: hidden;

          li {
            list-style-type: none;
            display: inline-block;
            float: left;
            width: 45%;
            position: relative;

            img {
              width: 100%;
            }

            .activeimg {
              display: none;
              position: absolute;
              width: 100%;
              height: 100%;
              top: 0%;
              left: 0%;
            }
          }

          li:nth-child(1) {
            margin-right: 10%;
          }
        }

        .active {
          .activeimg {
            display: block;
          }
        }

      }

      .tab1 {
        display: none;
        position: absolute;
        width: 75%;
        left: 12.5%;
        top: 26%;
        height: 60%;
        //              overflow: scroll;
        .tab1box {
          position: absolute;
          width: 100%;
          left: 0%;
          top: 0%;
          height: 100%;
          overflow: scroll;
        }

        .giftbox {
          //                  display: none;
          $mackets-popup-bg-width: 100%;
          position: relative;
          width: $mackets-popup-bg-width;
          top: 0;
          float: left; //      left: (100%-$mackets-popup-bg-width)/2;
          background: #FFFFFF;
          border-radius: 5px;
          margin-bottom: 3%;

          .giftimg {
            position: absolute;
            height: 55%;
            top: 22.5%;
            left: 5%;
          }

          .typeimg {
            position: absolute;
            width: 25%;
            top: 12%;
            left: 0%;
          }

          .giftintro {
            width: 45%;
            margin-left: 55%;
            text-align: left;
            font-size: 0.5rem;
            color: $main-font-color-g;
            line-height: 0.9rem;
            padding: 4% 0;

            span {
              font-size: 0.8rem;
              color: $main-font-color-r;
            }

            :before {
              content: ' ';
              position: absolute;
              width: 1px;
              height: 60%;
              top: 20%;
              left: 50%;
              background: #eaeaea;
            }

            .status1 {
              //                      position: absolute;
              width: 55%;
              text-align: center;
              background: none;
              border: 1px solid $main-font-color-r;
              border-radius: 3px;
              color: $main-font-color-r;
              padding: 4% 0%;
            }

            .status0, .status2 {
              width: 55%;
              text-align: center;
              background: none;
              border: 1px solid #dacec0;
              border-radius: 3px;
              color: #dacec0;
              padding: 4% 0%;
            }

            img {
              width: 55%;

            }
          }

        }

        .award2, .award4 {
          .giftimg {
            height: 75%;
            top: 17.5%;
            left: 10%;
          }
        }

        .award7, .award3, .award2, .award1 {
          .giftintro {
            padding: 8% 0;
          }
        }
      }

      .tab2 {
        display: none;
        position: absolute;
        width: 75%;
        left: 12.5%;
        top: 26%;
        height: 60%;

        .noshuo {
          display: none;

          p {
            width: 80%;
            position: absolute;
            left: 10%;
            top: 20%;
            font-size: .6rem;
            color: #FFFFFF;
          }

          .btn_wantsay {
            position: absolute;
            width: 50%;
            top: 50%;
            left: 25%;
          }
        }

        .showshuo {
          display: none;

          .giftbox {
            display: none;
            $mackets-popup-bg-width: 100%;
            position: relative;
            width: $mackets-popup-bg-width;
            top: 0;
            float: left; //      left: (100%-$mackets-popup-bg-width)/2;
            background: #FFFFFF;
            border-radius: 5px;
            margin-bottom: 3%;

            .giftimg {
              position: absolute;
              height: 55%;
              top: 22.5%;
              left: 12%;
            }

            .giftintro {
              width: 40%;
              margin-left: 60%;
              text-align: left;
              font-size: 0.8rem;
              color: $main-font-color-r;
              line-height: 0.9rem;
              padding: 13% 0;

            }

            .giftintro:before {
              content: ' ';
              position: absolute;
              width: 1px;
              height: 60%;
              top: 20%;
              left: 50%;
              background: #eaeaea;
            }

          }
        }
      }

      .see-more {
        display: none;
        bottom: -7%;
      }

      .urltip {
        position: absolute;
        width: 70%;
        left: 15%;
        bottom: -4%;
        color: #FFFFFF;
        font-size: .6rem;
        text-decoration: underline;
      }
    }
  }
}