.auditing.popup,.auditing2.popup{
    display: none;
//  >.bg{
//      position: absolute;
//      left: 0;
//      top: 0;
//      width: 100%;
//      height: 100%;
//  }
    .box{
        width: 100%;
        height: 100%;
        position: relative;
        @include display_flex(vertical);
        .wrap{
            position: relative;
            .content{
                position: relative;
                width: 100%;
                .bg{
                    width: 100%;
                }
                .title{
                    height: 5%;
                }
                .red-show{
                    $red-show-width: 25%;
                    position: absolute;
                    width: $red-show-width;
                    left: (100% - $red-show-width)/2;
                    top: 22%;
                }
                
                .up_done{
//                  display: none;
                    $input-wrap-width: 70%;
                        position: absolute;
                        width: $input-wrap-width;
                        margin-left: -($input-wrap-width/2);
                        left: 52%;
                        top: 50%;
                   p{
                       font-size: .6rem;
                       width: 100%;
                       text-align: center;
                       color: #FFFFFF;
                       line-height: 1.2rem;
                       span{
                           font-size: 1rem;
                           display: inline-block;
                           padding-bottom: .5rem;
                           color: $main-font-color-y;
                       }
                   }
                }
                    
                    .input-wrap{
                        $input-wrap-width: 74%;
                        position: absolute;
                        width: $input-wrap-width;
                        margin-left: -($input-wrap-width/2);
                        height: 20%;
                        left: 50%;
                        top: 48%;
                        background: url("../resources/img/bg/s-inputBg.png") 0 0 no-repeat;
                        background-size: 100% 100%;
                        font-size: .6rem;
                        text-align: left;
                        .phone{
                            @include input();
                            width: 100%;
                            height: 50%;
                            background: rgba(0,0,0,0);
                            text-indent: 1rem;
                        }
                        .code{
                            @include input();
                            width: 70%;
                            height: 50%;
                            background: rgba(0,0,0,0);
                            text-indent: 1rem;
                            box-sizing: border-box;
//                          padding-right: 50%;
                        }
                        
                        .send-btn{
                            $send-btn-height: 25%;
                            position: absolute;
                            right: .5rem;
                            width: 30%;
                            height: $send-btn-height;
                            bottom: $send-btn-height / 2;
                            @include display_flex(vertical);
                            background-color: #74ccef;
                            color: #FFFFFF;
                            
                            border-radius: .2rem;
                            &.unactive{
                                color: #ffffff;
                            }
                        }
                    }
                    .sure-btn{
                        $sure-btn-width: 48%;
                        position: absolute;
                        width: $sure-btn-width;
                        margin-left: -($sure-btn-width/2);
                        left: 50%;
                        top: 76%;
                    }
                    .btn_myprize{
                        $sure-btn-width: 23%;
                        position: absolute;
                        left: 50%;
                        width: $sure-btn-width;
                        margin-left: -($sure-btn-width/2);
                        top: 90%;
            //          animation-delay: 2.2s;
                    }
                    .message{
                            position: absolute;
                            top: 70%;
                            font-size: .5rem;
                            width: 100%;
                            text-align: center;
                            color: #FFFFFF;
                    }   

                
            }
        }
    }
}

